import './Vote.css';
import UserNavbar from '../../../Navbar/UserNavbar';
import * as React from 'react';
import { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import ScrollReveal from "scrollreveal";
import Backdrop from '@mui/material/Backdrop';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import {
    Card,
    CardContent,
    CardMedia,
    CardActions,
    Typography,
    Grid,
    Chip,
    Avatar,
    Container,
    Button
} from '@mui/material';
import PersonIcon from '@mui/icons-material/Person';
import SchoolIcon from '@mui/icons-material/School';
import { BASE_URL } from '../../../../helper';
import Cookies from 'js-cookie';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 700,
    bgcolor: 'rgb(255, 255, 255)',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

export default function CustomizedTables() {
    const revealRefBottom = useRef(null);
    const revealRefLeft = useRef(null);
    const revealRefTop = useRef(null);
    const revealRefRight = useRef(null);

    useEffect(() => {
        if (revealRefBottom.current) {
            ScrollReveal().reveal(revealRefBottom.current, {
                duration: 1000,
                delay: 300,
                distance: '50px',
                origin: 'bottom',
                easing: 'ease',
                reset: 'true',
            });
        }
    }, []);

    useEffect(() => {
        if (revealRefRight.current) {
            ScrollReveal().reveal(revealRefRight.current, {
                duration: 1000,
                delay: 300,
                distance: '50px',
                origin: 'right',
                easing: 'ease',
                reset: 'true',
            });
        }
    }, []);

    useEffect(() => {
        if (revealRefLeft.current) {
            ScrollReveal().reveal(revealRefLeft.current, {
                duration: 1000,
                delay: 300,
                distance: '50px',
                origin: 'left',
                easing: 'ease',
                reset: 'true',
            });
        }
    }, []);

    useEffect(() => {
        if (revealRefTop.current) {
            ScrollReveal().reveal(revealRefTop.current, {
                duration: 1000,
                delay: 300,
                distance: '50px',
                origin: 'top',
                easing: 'ease',
                reset: 'true',
            });
        }
    }, []);
    const [candidate, setCandidate] = useState([]);
    const voterid = Cookies.get('myCookie')

    useEffect(() => {
        // Fetch all candidates (single pool - no position grouping)
        axios.get(`${BASE_URL}/getCandidate`)
            .then((response) => {
                // Sort candidates by votes (highest first) for display
                const sorted = response.data.candidate.sort((a, b) => (b.votes || 0) - (a.votes || 0));
                setCandidate(sorted);
            })
            .catch(err => console.error("Error fetching data: ", err));
    }, [])

    const [voter, setVoter] = useState(null);
    const [isLoadingVoter, setIsLoadingVoter] = useState(true);
    const [votingSettings, setVotingSettings] = useState(null);
    const [timeRemaining, setTimeRemaining] = useState(null);

    useEffect(() => {
        if (!voterid) {
            console.error('No voter ID found in cookie');
            setIsLoadingVoter(false);
            return;
        }

        axios.get(`${BASE_URL}/getVoterbyID/${voterid}`)
            .then((response) => {
                if (response.data.success) {
                    setVoter(response.data.voter);
                } else {
                    console.error('Failed to fetch voter:', response.data.message);
                }
            })
            .catch(error => {
                console.error('Error fetching user data:', error);
            })
            .finally(() => {
                setIsLoadingVoter(false);
            });
    }, [voterid]);

    // Fetch voting settings and update countdown
    useEffect(() => {
        const fetchSettings = async () => {
            try {
                const response = await axios.get(`${BASE_URL}/api/votingSettings`);
                if (response.data.success) {
                    setVotingSettings(response.data.settings);
                }
            } catch (error) {
                console.error('Error fetching voting settings:', error);
            }
        };

        fetchSettings();
        // Refresh more frequently (every 5 seconds) to catch immediate changes
        const interval = setInterval(fetchSettings, 5000);

        return () => clearInterval(interval);
    }, []);

    // Update countdown timer
    useEffect(() => {
        if (!votingSettings) return;

        const updateCountdown = () => {
            const now = new Date();
            const endDate = new Date(votingSettings.endDate);
            const startDate = new Date(votingSettings.startDate);

            // Add a buffer (30 seconds) to account for timezone and processing delays
            // If we're within 30 seconds of start time, consider voting active
            const buffer = 30000; // 30 seconds in milliseconds

            if (now < (startDate.getTime() - buffer)) {
                setTimeRemaining({ type: 'untilStart', ms: startDate - now });
            } else if (now >= (startDate.getTime() - buffer) && now <= endDate && votingSettings.isActive) {
                setTimeRemaining({ type: 'remaining', ms: endDate - now });
            } else {
                setTimeRemaining({ type: 'ended', ms: 0 });
            }
        };

        updateCountdown();
        const interval = setInterval(updateCountdown, 1000); // Update every second

        return () => clearInterval(interval);
    }, [votingSettings]);

    const formatTimeRemaining = (ms) => {
        if (ms <= 0) return '0 days, 0 hours, 0 minutes';

        const days = Math.floor(ms / (1000 * 60 * 60 * 24));
        const hours = Math.floor((ms % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        const minutes = Math.floor((ms % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((ms % (1000 * 60)) / 1000);

        return `${days}d ${hours}h ${minutes}m ${seconds}s`;
    };

    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    // OTP verification states
    const [otpModalOpen, setOtpModalOpen] = React.useState(false);
    const [otpCode, setOtpCode] = React.useState('');
    const [otpLoading, setOtpLoading] = React.useState(false);
    const [otpRequested, setOtpRequested] = React.useState(false);
    const [selectedCandidateId, setSelectedCandidateId] = React.useState(null);
    const [otpMessage, setOtpMessage] = React.useState('');





    // Request OTP when user clicks vote
    const handleVote = async (id) => {
        try {
            // Check if voter data is loaded
            if (isLoadingVoter) {
                alert('Please wait while we load your voter information...');
                return;
            }

            // Check if voter ID exists in cookie
            if (!voterid) {
                alert('Session not found. Please login first, then visit the User page before voting.');
                window.location.href = '/Login';
                return;
            }

            // Check if voter data is loaded from API
            if (!voter) {
                alert('Could not load your voter information. Please visit the User page first, then return here to vote.');
                window.location.href = '/User';
                return;
            }

            const voterObjectId = voter._id || voterid;
            const objectIdRegex = /^[a-fA-F0-9]{24}$/;

            // Validate IDs
            if (!voterObjectId || !objectIdRegex.test(String(voterObjectId))) {
                alert('Invalid voter session. Please logout and login again.');
                return;
            }

            if (!id || !objectIdRegex.test(String(id))) {
                alert('Invalid candidate selection. Please try again.');
                return;
            }

            // Check if already voted
            if (voter.voteStatus) {
                alert("You Have Already Voted");
                return;
            }

            // Store selected candidate and open OTP modal
            setSelectedCandidateId(id);
            setOtpCode('');
            setOtpRequested(false);
            setOtpMessage('');
            setOtpModalOpen(true);

            // Request OTP
            setOtpLoading(true);
            const otpResponse = await axios.post(`${BASE_URL}/api/requestOTP`, {
                voterId: voterObjectId,
            });

            if (otpResponse.data.success) {
                setOtpRequested(true);
                setOtpMessage('OTP has been sent to your email address. Please check your inbox (and spam folder). The OTP is valid for 10 minutes.');
            } else {
                setOtpMessage(otpResponse.data.message || 'Failed to send OTP. Please try again or contact administrator.');
            }
        } catch (error) {
            console.error('Error requesting OTP:', error);
            const message = error?.response?.data?.message || 'Failed to request OTP. Please try again.';
            setOtpMessage(message);
            setOtpRequested(false);
        } finally {
            setOtpLoading(false);
        }
    }

    // Verify OTP and submit vote
    const handleVerifyOTPAndVote = async () => {
        try {
            if (!otpCode || otpCode.length !== 6) {
                setOtpMessage('Please enter a valid 6-digit OTP.');
                return;
            }

            if (!selectedCandidateId || !voter) {
                setOtpMessage('Invalid session. Please try again.');
                return;
            }

            const voterObjectId = voter._id || voterid;

            setOtpLoading(true);
            setOtpMessage('');

            // Submit vote with OTP (no position - single vote per voter)
            const response = await axios.post(`${BASE_URL}/vote`, {
                candidateId: selectedCandidateId,
                voterId: voterObjectId,
                otpCode: otpCode,
            });

            if (response.data.success) {
                // Update local voter state
                setVoter({ ...voter, voteStatus: true });
                setOtpModalOpen(false);
                handleOpen();
            } else {
                setOtpMessage(response.data.message || 'Voting failed. Please try again.');
            }
        } catch (error) {
            console.error('Error during vote:', error);
            const message = error?.response?.data?.message || 'Voting failed. Please try again.';
            setOtpMessage(message);
        } finally {
            setOtpLoading(false);
        }
    }




    // Add 30 second buffer to account for timezone/delay issues
    const now = new Date();
    const startDate = votingSettings ? new Date(votingSettings.startDate) : null;
    const endDate = votingSettings ? new Date(votingSettings.endDate) : null;
    const buffer = 30000; // 30 seconds buffer for safety

    const isVotingActive = votingSettings && votingSettings.isActive &&
        startDate && endDate &&
        now >= (startDate.getTime() - buffer) &&
        now <= endDate;

    return (
        <div className='Vote-Page'>
            <UserNavbar />
            <div className='candidate'>
                <h2 ref={revealRefLeft}>
                    {votingSettings?.electionTitle || 'WCU Student President Election'}
                </h2>
                <div className='Heading1' ref={revealRefRight}>
                    <p><span>GIVE</span> Your Vote</p>
                </div>

                {/* Voting Status Banner */}
                {votingSettings && (
                    <div className={`status-banner ${isVotingActive ? 'active' : (startDate && now < (startDate.getTime() - 30000)) ? 'pending' : 'ended'}`}>
                        {isVotingActive ? (
                            <>
                                <Typography variant="h5" sx={{ color: 'var(--color-success)', fontWeight: 'var(--font-weight-bold)', mb: 1 }}>
                                    Voting is ACTIVE
                                </Typography>
                                {timeRemaining?.type === 'remaining' && (
                                    <Typography variant="body1" sx={{ color: 'var(--text-primary)', fontWeight: 'var(--font-weight-medium)' }}>
                                        Time Remaining: <span style={{ color: 'var(--color-primary)', fontWeight: 'var(--font-weight-extrabold)' }}>{formatTimeRemaining(timeRemaining.ms)}</span>
                                    </Typography>
                                )}
                            </>
                        ) : (startDate && now < (startDate.getTime() - 30000)) ? (
                            <>
                                <Typography variant="h5" sx={{ color: 'var(--color-primary)', fontWeight: 'var(--font-weight-bold)', mb: 1 }}>
                                    Election Scheduled
                                </Typography>
                                <Typography variant="body2" sx={{ color: 'var(--text-secondary)', mb: 1 }}>
                                    Begins on: <strong>{startDate.toLocaleString()}</strong>
                                </Typography>
                                {timeRemaining?.type === 'untilStart' && (
                                    <Typography variant="body1" sx={{ color: 'var(--text-primary)', fontWeight: 'var(--font-weight-medium)' }}>
                                        Starts in: <span style={{ color: 'var(--color-primary)', fontWeight: 'var(--font-weight-extrabold)' }}>{formatTimeRemaining(timeRemaining.ms)}</span>
                                    </Typography>
                                )}
                            </>
                        ) : (
                            <>
                                <Typography variant="h5" sx={{ color: 'var(--color-error)', fontWeight: 'var(--font-weight-bold)', mb: 1 }}>
                                    Voting Has Ended
                                </Typography>
                                <Typography variant="body1" sx={{ color: 'var(--text-secondary)' }}>
                                    Concluded on: <strong>{new Date(votingSettings.endDate).toLocaleString()}</strong>
                                </Typography>
                            </>
                        )}
                        {!votingSettings.isActive && (
                            <Typography variant="body2" sx={{ color: 'var(--color-error)', mt: 2, fontWeight: 'var(--font-weight-bold)' }}>
                                ⚠️ System is currently suspended by administrator.
                            </Typography>
                        )}
                    </div>
                )}

                {!voterid && (
                    <div className="status-banner ended" style={{ padding: '30px' }}>
                        <Typography variant="h5" sx={{ color: 'var(--color-error)', mb: 2 }}>Session Expired</Typography>
                        <Button
                            variant="contained"
                            href="/Login"
                            sx={{ borderRadius: 'var(--radius-full)', background: 'var(--grad-primary)' }}
                        >
                            Return to Login
                        </Button>
                    </div>
                )}

                {voterid && !voter && !isLoadingVoter && (
                    <div className="status-banner pending" style={{ padding: '30px' }}>
                        <Typography variant="h5" sx={{ color: 'var(--color-primary)', mb: 2 }}>Profile Sync Required</Typography>
                        <Button
                            variant="contained"
                            href="/User"
                            sx={{ borderRadius: 'var(--radius-full)', background: 'var(--grad-primary)' }}
                        >
                            Initialize Session
                        </Button>
                    </div>
                )}

                {isLoadingVoter && (
                    <div style={{
                        padding: '20px',
                        margin: '20px',
                        textAlign: 'center'
                    }}>
                        <p>Loading your voter information...</p>
                    </div>
                )}
                {/* OTP Verification Modal */}
                <Modal
                    className='VoteContent'
                    aria-labelledby="otp-modal-title"
                    aria-describedby="otp-modal-description"
                    open={otpModalOpen}
                    onClose={() => {
                        if (!otpLoading) {
                            setOtpModalOpen(false);
                            setOtpCode('');
                            setOtpMessage('');
                            setOtpRequested(false);
                        }
                    }}
                    closeAfterTransition
                    slots={{ backdrop: Backdrop }}
                    slotProps={{
                        backdrop: {
                            timeout: 500,
                        },
                    }}
                >
                    <Fade in={otpModalOpen} className='VoteGivenBox'>
                        <Box sx={style} className="MessageBox glass-modal">
                            <Typography variant="h4" sx={{
                                fontWeight: 'var(--font-weight-bold)',
                                color: 'var(--color-primary)',
                                mb: 2,
                                textAlign: 'center'
                            }}>
                                Identity Verification
                            </Typography>
                            <Typography variant="body1" sx={{ textAlign: 'center', mb: 4, color: 'var(--text-secondary)' }}>
                                A One-Time Password has been sent to your registered email. Enter it below to authorize your vote.
                            </Typography>

                            {otpMessage && (
                                <Box sx={{
                                    p: 2,
                                    mb: 3,
                                    borderRadius: 'var(--radius-md)',
                                    backgroundColor: otpMessage.includes('sent') || otpMessage.includes('success')
                                        ? 'rgba(76, 206, 172, 0.1)'
                                        : 'rgba(219, 79, 74, 0.1)',
                                    color: 'var(--text-primary)',
                                    border: `1px solid ${otpMessage.includes('sent') || otpMessage.includes('success') ? 'var(--color-success)' : 'var(--color-error)'}`,
                                    fontSize: '0.9rem'
                                }}>
                                    {otpMessage}
                                </Box>
                            )}

                            <Box sx={{ mb: 4 }}>
                                <input
                                    type="text"
                                    value={otpCode}
                                    onChange={(e) => {
                                        const value = e.target.value.replace(/\D/g, '').slice(0, 6);
                                        setOtpCode(value);
                                        setOtpMessage('');
                                    }}
                                    placeholder="••••••"
                                    className="input-standard"
                                    maxLength={6}
                                    disabled={otpLoading || !otpRequested}
                                    style={{
                                        fontSize: '32px',
                                        textAlign: 'center',
                                        letterSpacing: '12px',
                                        height: '60px'
                                    }}
                                />
                                {otpRequested && (
                                    <Typography variant="caption" sx={{ color: 'var(--text-muted)', display: 'block', mt: 1, textAlign: 'center' }}>
                                        Valid for 10 minutes • Check spam if not received
                                    </Typography>
                                )}
                            </Box>

                            <Box sx={{ display: 'flex', gap: 2, mt: 2 }}>
                                <Button
                                    fullWidth
                                    variant="contained"
                                    onClick={handleVerifyOTPAndVote}
                                    disabled={otpLoading || !otpRequested || otpCode.length !== 6}
                                    sx={{
                                        py: 1.5,
                                        borderRadius: 'var(--radius-md)',
                                        background: 'var(--grad-primary)',
                                        fontWeight: 'var(--font-weight-bold)'
                                    }}
                                >
                                    {otpLoading ? 'Verifying...' : 'Authenticate Vote'}
                                </Button>
                                <Button
                                    fullWidth
                                    variant="outlined"
                                    onClick={() => {
                                        if (!otpLoading) {
                                            setOtpModalOpen(false);
                                            setOtpCode('');
                                            setOtpMessage('');
                                            setOtpRequested(false);
                                        }
                                    }}
                                    disabled={otpLoading}
                                    sx={{
                                        py: 1.5,
                                        borderRadius: 'var(--radius-md)',
                                        border: '1px solid var(--border-color)',
                                        color: 'var(--text-primary)'
                                    }}
                                >
                                    Cancel
                                </Button>
                            </Box>
                        </Box>
                    </Fade>
                </Modal>

                {/* Success Modal */}
                <Modal
                    className='VoteContent'
                    open={open}
                    onClose={handleClose}
                    closeAfterTransition
                >
                    <Fade in={open} className='VoteGivenBox'>
                        <Box sx={style} className="MessageBox glass-modal" style={{ textAlign: 'center' }}>
                            <Box sx={{ color: 'var(--color-success)', mb: 2 }}>
                                <CheckCircleIcon sx={{ fontSize: 80 }} />
                            </Box>
                            <Typography variant="h4" sx={{ color: 'var(--text-primary)', fontWeight: 'var(--font-weight-bold)', mb: 1 }}>
                                Success!
                            </Typography>
                            <Typography variant="h6" sx={{ color: 'var(--text-secondary)', mb: 4 }}>
                                Your vote has been recorded securely.
                            </Typography>
                            <Button
                                variant="contained"
                                onClick={handleClose}
                                href="/User"
                                sx={{
                                    minWidth: '200px',
                                    borderRadius: 'var(--radius-full)',
                                    background: 'var(--grad-primary)',
                                    py: 1.2
                                }}
                            >
                                Understood
                            </Button>
                        </Box>
                    </Fade>
                </Modal>

                {/* Info Banner */}
                <Box className="info-banner" sx={{ maxWidth: '1000px', mx: 'auto' }}>
                    <Typography variant="body1" sx={{ color: 'var(--color-primary)', fontWeight: 'var(--font-weight-bold)', textAlign: 'center' }}>
                        ⚖️ Single Pool Election: Positions will be automatically assigned based on total vote counts after the period ends.
                    </Typography>
                </Box>

                {/* All Candidates Grid */}
                {candidate.length > 0 ? (
                    <Container maxWidth="xl" sx={{ mt: 6, mb: 10 }} ref={revealRefBottom}>
                        <Box sx={{
                            p: 4,
                            background: 'var(--grad-surface)',
                            borderLeft: '6px solid var(--color-primary)',
                            mb: 5,
                            borderRadius: 'var(--radius-lg)',
                            boxShadow: 'var(--shadow-md)',
                            border: '1px solid var(--border-color)'
                        }}>
                            <Typography variant="h3" sx={{ color: 'var(--color-primary)', fontWeight: 'var(--font-weight-bold)', mb: 1 }}>
                                Candidate Roster
                            </Typography>
                            <Typography variant="h6" sx={{ color: 'var(--text-secondary)' }}>
                                Review the qualifications and cast your decisive vote.
                            </Typography>
                        </Box>

                        <Grid container spacing={4}>
                            {candidate.map((row) => (
                                <Grid item xs={12} sm={6} md={4} lg={3} key={row._id || row.name}>
                                    <Card
                                        className="card-standard"
                                        sx={{
                                            height: '100%',
                                            display: 'flex',
                                            flexDirection: 'column',
                                            overflow: 'hidden',
                                            backgroundColor: 'var(--card-bg)',
                                            border: '1px solid var(--border-color)'
                                        }}
                                    >
                                        <Box sx={{ position: 'relative' }}>
                                            <CardMedia
                                                component="img"
                                                height="300"
                                                image={row.img ? `${BASE_URL}${row.img}` : "https://via.placeholder.com/250?text=No+Photo"}
                                                alt={row.name}
                                                sx={{ objectFit: 'cover' }}
                                            />
                                            <Box sx={{
                                                position: 'absolute',
                                                bottom: 0,
                                                left: 0,
                                                right: 0,
                                                p: 2,
                                                background: 'linear-gradient(to top, rgba(0,0,0,0.8), transparent)'
                                            }}>
                                                <Typography variant="h6" sx={{ color: 'white', fontWeight: 'var(--font-weight-bold)' }}>
                                                    {row.name}
                                                </Typography>
                                            </Box>
                                        </Box>

                                        <CardContent sx={{ flexGrow: 1, p: 3 }}>
                                            <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                                                <SchoolIcon sx={{ fontSize: 18, mr: 1, color: 'var(--color-primary)' }} />
                                                <Typography variant="body2" sx={{ color: 'var(--text-secondary)', fontWeight: 'var(--font-weight-medium)' }}>
                                                    {row.party || row.department}
                                                </Typography>
                                            </Box>

                                            <Box sx={{ display: 'flex', gap: 1, mb: 3 }}>
                                                <Chip
                                                    icon={<PersonIcon sx={{ fontSize: 16 }} />}
                                                    label={`CGPA: ${row.cgpa !== undefined ? row.cgpa : (row.age || 'N/A')}`}
                                                    sx={{
                                                        borderRadius: 'var(--radius-sm)',
                                                        backgroundColor: 'rgba(104, 112, 250, 0.1)',
                                                        color: 'var(--color-primary)',
                                                        fontWeight: 'var(--font-weight-bold)',
                                                        border: '1px solid rgba(104, 112, 250, 0.2)'
                                                    }}
                                                />
                                            </Box>

                                            <Typography variant="body2" sx={{
                                                color: 'var(--text-primary)',
                                                lineHeight: 1.6,
                                                minHeight: '80px',
                                                fontStyle: 'italic'
                                            }}>
                                                {row.bio ? `"${row.bio}"` : "Commitment to academic excellence and student welfare."}
                                            </Typography>
                                        </CardContent>

                                        <CardActions sx={{ p: 3, pt: 0 }}>
                                            <Button
                                                fullWidth
                                                variant="contained"
                                                size="large"
                                                onClick={() => handleVote(row._id)}
                                                disabled={
                                                    !voter ||
                                                    isLoadingVoter ||
                                                    (voter && voter.voteStatus) ||
                                                    !isVotingActive
                                                }
                                                sx={{
                                                    borderRadius: 'var(--radius-full)',
                                                    py: 1.5,
                                                    fontWeight: 'var(--font-weight-bold)',
                                                    background: 'var(--grad-primary)',
                                                    boxShadow: '0 4px 12px var(--button-shadow)',
                                                    '&:hover': { background: 'var(--color-primary-dark)' },
                                                    '&:disabled': { backgroundColor: 'var(--bg-secondary)', color: 'var(--text-muted)' }
                                                }}
                                            >
                                                {!isVotingActive
                                                    ? 'Election Closed'
                                                    : voter && voter.voteStatus
                                                        ? 'Already Voted'
                                                        : 'Cast Vote'
                                                }
                                            </Button>
                                        </CardActions>
                                    </Card>
                                </Grid>
                            ))}
                        </Grid>
                    </Container>
                ) : (
                    <div style={{ padding: '40px', textAlign: 'center', backgroundColor: 'rgba(255,243,205,0.9)', borderRadius: '8px', margin: '20px auto', maxWidth: '600px' }}>
                        <p style={{ fontSize: '18px', color: '#856404', margin: 0 }}>No candidates registered yet.</p>
                    </div>
                )}
            </div>

        </div>
    );
}
